* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 20px 20px;
}

h1 {
  text-align: center;
  background-color: #fff8dc;
  margin: 0 0 10px;
  padding: 25px;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

input {
  max-width: 300px;
  border-width: 2px;
  border-color: rgba(211, 211, 211, .5);
  border-radius: 3px;
  outline: none;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
}

button {
  border-radius: 3px;
}

button[type="submit"], button.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  padding: 10px 20px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 10px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  padding: 10px;
}

.photo-card img {
  max-width: 100%;
}

.info {
  margin-top: 10px;
}

.info-item {
  margin-bottom: 5px;
}

